import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';

@Component({
  selector: 'app-filter-menu-tratamientos',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  public proveedorespp: any[];
  constructor(@Inject('MENU_DATA') public data: any, private _menuService: JedsMenuService) { }

  ngOnInit(): void {
    /**
    * Formulario de filtros
    */
    this.formFilters = new FormGroup({
      activo: new FormControl(('')),
    });
    this.formFilters.patchValue(this.data.filter);
  }

  resetFilters() {
    this.formFilters.reset({
      activo: -1
    });
  }

  onCloseMenu(data: any = null) {
    this._menuService.close(data);
  }
}