import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { ITratamiento } from '../models/tratamiento';

@Injectable()
export class TratamientosService {
    constructor(private http: HttpClient) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los tratamientos activos.
     * @returns tratamientos activos
     */
    getTratamientos(filter?: any): Observable<ITratamiento[]> {
        let f = '?';
        f += filter == 'desc' ? 'sort=-descripcion' : 'sort=descripcion'

        return this.http.get<ITratamiento[]>(`${environment.administracion.tratamientos}${f}`);
    }
    /**
     * Funcion en el Servicio 
     * @param tratamiento 
     * @returns Un tratamiento nuevo
     */
    saveTratamiento(tratamiento: ITratamiento): Observable<ITratamiento> {
        return this.http.post<ITratamiento>(`${environment.administracion.tratamientos}`, tratamiento);
    }
    /**
     * Funcion en el servicio
     * @param tratamiento 
     * @returns tratamiento actualizado
     */
    updateTratamiento(tratamiento: ITratamiento): Observable<ITratamiento> {
        return this.http.put<ITratamiento>(`${environment.administracion.tratamientos}` + '/' + tratamiento.idtratamiento, tratamiento);
    }
    /**
     * Funcion en el servicio
     * @param idtratamiento 
     * @returns Elimina el tratamiento
     */
    deleteTratamiento(idtratamiento: number): Observable<ITratamiento> {
        return this.http.delete<ITratamiento>(environment.administracion.tratamientos + '/' + idtratamiento);
    }

}